<template>
<div class="p-b-5 p-e-5 b-fs x6 m:x5 l:x3 xl:x2">
    <div class="crd -o-h l20 d19">
        <post-router-link :to="'/post/' + post.id" class="l:16/9 1/1 rlt b">
            <LoadingWidget class="rto b-c s-d"></LoadingWidget>
            <img :src="post.photo" class="rto -obj-cv -z" />
        </post-router-link>
        <div class="b-c b-j-sb h-10">
            <div class="b-a-c h-8">
                <div class="m-x-5 -r-c -b -b-2 u-5 c-s-t --s-n b-fs"></div>
                <div class="CASE ...">
                    <div class="t-7 o-8 m-b-4">{{ post.title }}</div>
                    <div class="t-8 o-5">{{ post.outline }}</div>
                </div>
            </div>
            <div class="-i u-8 unt btn-n .e -r-s-c">
                bookmark_border
            </div>
        </div>
    </div>
</div>
</template>

<script>
import {
    mapState,
    mapGetters
} from "vuex";
import LoadingWidget from "../Loading";
export default {
    name: "postCardWidget",
    props: {
        post: Object
    },
    data() {
        return {
            option: null,
            quantity: 1
        };
    },
    computed: {
        ...mapState(["cart"]),
        ...mapGetters(["itemExists", "toMoney"])
    },
    components: {
        LoadingWidget
    },
    methods: {
        addCartItem() {
            this.$store.commit("ADD_CART_ITEM", {
                post: this.post,
                quantity: this.quantity,
                option: this.option
            });
        },
        removeCartItem() {
            this.$store.commit("REMOVE_CART_ITEM", {
                post: this.post,
                option: this.option
            });
        }
    }
};
</script>
