<template>
  <div class="p-b-5 b-fs" v-if="item.product">
    <div class="crd -o-h l20 d19">
      <div class="b-c b-j-sb h-10">
        <div class="b-a-c h-8">
          <router-link :to="'/product/' + item.product.id">
            <img :src="item.product.photos[0]" class="-r-c u-8 -obj-cv m-x-4" />
          </router-link>
          <div class="CASE ...">
            <div class="t-7 o-8 m-b-4">{{ item.product.name }}</div>
            <div class="t-8 o-5">{{ toMoney(item.product.price) }}</div>
          </div>
        </div>
        <div
          v-if="!itemExists(item.product)"
          class="-i u-8 unt btn-n .e -r-s-c"
          @click="addCartItem"
        >
          bookmark_border
        </div>
        <div
          v-if="itemExists(item.product)"
          class="-i u-8 unt btn-n .e -r-s-c c-rd-t"
          @click="removeCartItem"
        >
          bookmark
        </div>
      </div>
      <div class="ln"></div>
      <!-- <div class="b-c b-j-sb">
        <div
          :class="(item.quantity > 1 ? '' : 's-d ') + '-i u-8 unt btn-n b-fs'"
          @click="quantity--"
        >
          remove
        </div>
        <input v-model="quantity" class="fld-n a-c w-f" />
        <div
          :class="
            (item.product.quantity > item.quantity ? '' : 's-d ') +
              '-i u-8 unt btn-n b-fs'
          "
          @click="quantity++"
        >
          add
        </div>
      </div> -->
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
// import loadingWidget from "../loading";
export default {
  name: "cartItemWidget",
  props: {
    item: Object
  },
  data() {
    return {
      quantity: this.item.quantity,
      option: null
    };
  },
  computed: {
    ...mapState(["cart"]),
    ...mapGetters(["itemExists", "toMoney"])
  },
  components: {
    // loadingWidget
  },
  watch: {
    quantity: function(to) {
      if (to >= 1 && to <= this.item.product.quantity) {
        this.$store.commit("SET_CART_ITEM_QUANTITY", {
          item: this.item,
          quantity: to
        });
      } else {
        this.quantity = to < 1 ? 0 : this.item.product.quantity;
      }
    }
  },
  methods: {
    setCartItemQuantity(quantity = 1) {
      if (quantity >= 1 && quantity <= this.item.product.quantity) {
        if (this.quantity) this.quantity = quantity;
      }
    },
    setItem(quantity = this.quantity, option = this.option) {
      this.$store.commit("SET_CART_ITEM", this.item, {
        product: this.item.product,
        option: option,
        quantity: quantity
      });
    },
    addCartItem() {
      this.$store.commit("ADD_CART_ITEM", {
        product: this.item.product,
        quantity: this.quantity,
        option: this.option
      });
    },
    removeCartItem() {
      this.$store.commit("REMOVE_CART_ITEM", this.item);
    }
  },
  mounted() {}
};
</script>
