</template>

<script>
import {
    mapMutations,
    mapState
} from "vuex";
// import NavBarListHWidget from "../widgets/types/NavBarList";
export default {
    name: "HeaderPart",
    props: {},
    data() {
        return {
            option: null,
            query: ""
        };
    },
    computed: {
        ...mapState(["theme"])
    },
    components: {
        // NavBarListHWidget
    },
    methods: {
        ...mapMutations(["TOGGLE_THEME_BRIGHTNESS", "TOGGLE_SIDEBAR"]),
        searchQuery() {
            this.$router.push({
                name: "Search",
                params: {
                    query: this.query
                }
            });
        },
        addCartItem() {
            this.$store.commit("ADD_CART_ITEM", {
                product: this.product,
                quantity: this.quantity,
                option: this.option
            });
        },
        removeCartItem() {
            this.$store.state.cart
                .filter(x => x.product.id == this.product.id)
                .forEach(_item => {
                    this.$store.commit("REMOVE_CART_ITEM", {
                        product: _item.product,
                        option: _item.option
                    });
                });
        },
        typesToNavBarItems(types) {
            var homeItem = {
                to: "/",
                name: "Home",
                icon: "home"
            };
            var _data = types.map(type => {
                return {
                    to: "/type/" + type.id,
                    name: type.name,
                    icon: null
                };
            });
            return [homeItem, ..._data];
        }
    },
    created() {
        this.$store.dispatch("loadTypes");
    }
};
</script>

