import { createStore } from "vuex";

const axios = require("axios");

export default createStore({
  state: {
    theme: {
      brightness: "light"
    },
    showSidebar: false,
    currency: "USD",
    cart: {},
    types: [],
    container: false,
    loading: false
  },
  mutations: {
    SET_THEME(state, theme) {
      state.theme = theme;
    },
    TOGGLE_THEME_BRIGHTNESS(state) {
      state.theme.brightness =
        state.theme.brightness == "dark" ? "light" : "dark";
    },
    TOGGLE_SIDEBAR(state) {
      state.showSidebar = !state.showSidebar;
    },

    //TYPES
    SET_TYPES(state, data) {
      state.types = data;
      localStorage.types = JSON.stringify(state.types);
      // state.cart.unshift({
      //   item: item,
      //   quantity: quantity,
      //   option: option
      // });
    },
    //CART
    SET_CART(state, data) {
      state.cart = { ...data };
      localStorage.cart = JSON.stringify(state.cart);
      // state.cart.unshift({
      //   item: item,
      //   quantity: quantity,
      //   option: option
      // });
    },
    ADD_CART_ITEM(state, item) {
      console.log(item);
      state.cart[item.product.id + "-" + item.option] = item;
      localStorage.cart = JSON.stringify(state.cart);
      // state.cart.unshift({
      //   item: item,
      //   quantity: quantity,
      //   option: option
      // });
    },
    REMOVE_CART_ITEM(state, item) {
      delete state.cart[item.product.id + "-" + item.option];
      localStorage.cart = JSON.stringify(state.cart);
    },
    SET_CART_ITEM_QUANTITY(state, data) {
      var cartItem = state.cart[data.item.product.id + "-" + data.item.option];
      if (
        data.quantity >= 1 &&
        data.quantity <= data.item.product.quantity &&
        cartItem
      )
        cartItem.quantity = data.quantity;
      localStorage.cart = JSON.stringify(state.cart);
    },
    SET_CART_ITEM(state, from, to) {
      state.cart[from.product.id + "-" + from.option] = to;
      localStorage.cart = JSON.stringify(state.cart);
    },
    SET_LOADING(state, isLoading) {
      state.loading = isLoading;
    }
  },
  getters: {
    itemExists: state => (product, option = null) => {
      return !!state.cart[product.id + "-" + option];
    },
    toMoney: state => price => {
      return price + " " + state.currency;
    }
  },
  actions: {
    loadCart({ commit }) {
      var cart;
      try {
        cart = JSON.parse(localStorage.cart || "{}");
      } catch (error) {
        cart = {};
      }
      commit("SET_CART", cart);
    },
    destroyCart({ commit }) {
      commit("SET_CART", {});
    },
    loadTypes({ commit }) {
      commit("SET_LOADING", true);
      axios
        .get("http://guide.eg-1.com/api/v1/data/types/min")
        .then(response => {
          commit("SET_LOADING", false);

          commit("SET_TYPES", response.data);
        })
        .catch(error => {
          commit("SET_LOADING", false);
          console.error(error);
        });
    },
    loadTypeWithCates({ commit }, options) {
      if (options.type_id) {
        commit("SET_LOADING", true);
        return axios
          .get(
            "http://guide.eg-1.com/api/v1/data/types/get?type_id=" +
              options.type_id
          )
          .then(response => {
            commit("SET_LOADING", false);
            options.callback(response.data.cates);
          })
          .catch(error => {
            commit("SET_LOADING", false);
            console.error(error);
          });
      }
    },
    loadCateById({ commit }, options) {
      if (options.cate_id) {
        commit("SET_LOADING", true);
        return axios
          .get(
            "http://guide.eg-1.com/api/v1/data/cates/get?cate_id=" +
              options.cate_id
          )
          .then(response => {
            commit("SET_LOADING", false);
            options.callback(response.data);
          })
          .catch(error => {
            commit("SET_LOADING", false);
            console.error(error);
          });
      }
    },
    loadProductById({ commit }, options) {
      if (options.product_id) {
        commit("SET_LOADING", true);
        return axios
          .get(
            "http://guide.eg-1.com/api/v1/data/products/get?product_id=" +
              options.product_id
          )
          .then(response => {
            commit("SET_LOADING", false);
            options.callback(response.data);
          })
          .catch(error => {
            commit("SET_LOADING", false);
            console.error(error);
          });
      }
    },
    loadProductsByCateId({ commit }, options) {
      if (options.cate_id) {
        commit("SET_LOADING", true);
        return axios
          .get(
            "http://guide.eg-1.com/api/v1/data/products?cate_id=" +
              options.cate_id +
              "&page=" +
              options.page +
              "&max=12"
          )
          .then(response => {
            commit("SET_LOADING", false);
            options.callback(response.data);
          })
          .catch(error => {
            commit("SET_LOADING", false);
            console.error(error);
          });
      }
    },
    loadProductsBySearchQuery({ commit }, options) {
      if (options.query) {
        commit("SET_LOADING", true);
        return axios
          .get(
            "http://guide.eg-1.com/api/v1/data/products?q=" +
              options.query +
              "&page=" +
              options.page +
              "&max=12"
          )
          .then(response => {
            commit("SET_LOADING", false);
            options.callback(response.data);
          })
          .catch(error => {
            commit("SET_LOADING", false);
            console.error(error);
          });
      }
    }
  },
  modules: {}
});
