<template>
  <div class="p-b-5 p-e-5 b-fs x6 m:x5 l:x3 xl:x2">
    <div class="crd -o-h l20 d19">
      <div class="16/9 rlt b">
        <LoadingWidget class="rto b-c s-d"></LoadingWidget>
        <img class="rto -obj-cv -z" :src="offer.photo" />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
import LoadingWidget from "../Loading";
export default {
  name: "OfferCardWidget",
  props: {
    offer: Object
  },
  data() {
    return {
      option: null,
      quantity: 1
    };
  },
  computed: {
    ...mapState(["cart"]),
    ...mapGetters(["itemExists", "toMoney"])
  },
  components: {
    LoadingWidget
  },
  methods: {
    addCartItem() {
      this.$store.commit("ADD_CART_ITEM", {
        offer: this.offer,
        quantity: this.quantity,
        option: this.option
      });
    },
    removeCartItem() {
      this.$store.commit("REMOVE_CART_ITEM", {
        offer: this.offer,
        option: this.option
      });
    }
  }
};
</script>
