<template>
  <main class="m-y-6">
    <CatesGridWidget :cates="cates"></CatesGridWidget>
  </main>
</template>

<script>
// @ is an alias to /src
import { mapActions } from "vuex";
import CatesGridWidget from "../widgets/cates/CatesGrid";
export default {
  name: "TypeView",
  components: {
    CatesGridWidget
  },
  computed: {
    ...mapActions(["loadTypeWithCates"])
  },
  data() {
    return {
      cates: null
    };
  },
  methods: {
    updateCates(data) {
      this.cates = data;
    }
  },
  watch: {
    "$route.params.type_id": {
      handler: function(type_id) {
        this.$store.dispatch("loadTypeWithCates", {
          type_id: type_id,
          callback: this.updateCates
        });
      },
      deep: true,
      immediate: true
    }
  },
  created() {}
};
</script>
