<template>
  <main class="m-t-5">
    <CntKWidget v-if="product">
      <div class="g">
        <ProductPhotosKWidget
          class="l:x6"
          :photos="product.photos"
        ></ProductPhotosKWidget>
        <div class="l:x6">
          <div class="b  m-y-5">
            <div class="m-x-5 -r-c -b -b-2 u-5 c-s-t b-fs"></div>
            <div>
              <div class="CASE t-6 o-8">
                {{ toMoney(product.price - product.discount) }}
              </div>
              <div class="b p-y-4">
                <div
                  v-if="product.discount > 0"
                  style="text-decoration: line-through;"
                  class="o-7"
                >
                  {{ toMoney(product.price) }}
                </div>
                <div v-if="product.discount > 0" class="c-20-t c-ylw m-x-4">
                  -{{ ((product.discount * 100) / product.price).toFixed(2) }}%
                </div>
              </div>
            </div>
          </div>
          <div class="ln"></div>

          <div class="b  m-y-5">
            <div class="m-x-5 -r-c -b -b-2 u-5 c-s-t b-fs"></div>
            <div>
              <div class="CASE t-7 o-8">details</div>
              <p>
                {{ product.details }}
              </p>
            </div>
          </div>
          <div class="ln"></div>

          <div class="b  m-y-5">
            <div class="m-x-5 -r-c -b -b-2 u-5 c-s-t b-fs"></div>
            <div>
              <div class="CASE t-7 o-8">options</div>
              <div class="b m-t-5">
                <div
                  class="unt btn btn-n -b -r-c CASE m-e-4 m-b-4"
                  v-for="(option, index) in getProductOptions(product.options)"
                  :key="index"
                >
                  {{ option }}
                </div>
              </div>
            </div>
          </div>
          <dib class="p-5  -d-b"
            ><CartItemWidget :item="pikItem()"></CartItemWidget
          ></dib>
        </div>
      </div>
    </CntKWidget>
  </main>
</template>

<script>
// @ is an alias to /src
import { mapActions, mapGetters, mapState } from "vuex";
import CartItemWidget from "../widgets/cart/CartItem";
import CntKWidget from "../widgets/kimai/Cnt";
import ProductPhotosKWidget from "../widgets/kimai/store/ProductPhotos";
export default {
  name: "ProductView",
  components: {
    ProductPhotosKWidget,
    CntKWidget,
    CartItemWidget
  },
  computed: {
    ...mapActions(["loadTypeWithProducts"]),
    ...mapState(["loading"]),
    ...mapGetters(["toMoney"]),
    page() {
      return this.$route.query.page || 1;
    }
  },
  data() {
    return {
      product: null,
      products: null,
      hasNextPage: true
    };
  },
  methods: {
    updateProduct(data) {
      this.product = data;
    },
    loadMoreProducts() {
      if (this.hasNextPage) {
        this.$router.push({ query: { page: this.page * 1 + 1 } });
      }
    },
    getProductOptions(options) {
      if (typeof options === "string") {
        return options.replaceAll("", ",").split(",");
      } else {
        return options;
      }
    },
    pikItem() {
      return (
        this.$store.state.cart[this.product.id + "-" + null] || {
          product: this.product,
          quantity: 1,
          option: null
        }
      );
    }
  },
  watch: {
    "$route.params.product_id": {
      handler: function(to) {
        console.log(this.page);
        this.$store.dispatch("loadProductById", {
          product_id: to,
          callback: this.updateProduct
        });
      },
      deep: true,
      immediate: true
    }
  },
  created() {}
};
</script>
