<template>
  <main>
    <div class="h-14 rlt -o-h m-b-6">
      <img
        src="https://www.dominos.co.in/theme2/front/images/menu-images/my-nonveg.jpg"
        class="-obj-bg o-4"
      />
      <div class="abs .b w-f" v-if="cate">
        <div class="l:x10 m:x12 p-4 m-a g">
          <div class="b-a-c">
            <router-link
              :to="'/type/' + cate.type_id"
              class="-i unt -r-c u-8 b-fs -i-x"
              >arrow_back</router-link
            >
            <div class="CASE p-e-5">
              <div class="t-6 o-8 ...">{{ cate.name }}</div>
              <div class="t-8 o-5 ...">
                {{ cate.details }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <CntKWidget>
      <ProductsGridWidget :products="products"></ProductsGridWidget>
      <div
        :class="
          (hasNextPage && !loading ? '' : 's-d ') +
            'unt btn btn-n w-f t-w9 a-c CASE'
        "
        @click="loadMoreProducts"
      >
        more
      </div>
    </CntKWidget>
  </main>
</template>

<script>
// @ is an alias to /src
import { mapActions, mapState } from "vuex";
import CntKWidget from "../widgets/kimai/Cnt";
import ProductsGridWidget from "../widgets/products/ProductsGrid";
export default {
  name: "CateView",
  components: {
    ProductsGridWidget,
    CntKWidget
  },
  computed: {
    ...mapActions(["loadTypeWithCates"]),
    ...mapState(["loading"]),
    page() {
      return this.$route.query.page || 1;
    }
  },
  data() {
    return {
      cate: null,
      products: null,
      hasNextPage: true
    };
  },
  methods: {
    updateProducts(data) {
      this.products = data.data;
      this.hasNextPage = this.page * 1 + 1 <= data.last_page;
    },
    updateCate(data) {
      this.cate = data;
    },
    loadMoreProducts() {
      if (this.hasNextPage) {
        this.$router.push({ query: { page: this.page * 1 + 1 } });
      }
    }
  },
  watch: {
    "$route.params.cate_id": {
      handler: function(to) {
        console.log(this.page);
        this.$store.dispatch("loadProductsByCateId", {
          cate_id: to,
          page: this.page,
          callback: this.updateProducts
        });
        if (!this.cate || this.cate.id != to) {
          this.$store.dispatch("loadCateById", {
            cate_id: to,
            callback: this.updateCate
          });
        }
      },
      deep: true,
      immediate: true
    }
  },
  created() {}
};
</script>
